import React, { Fragment, useState, useEffect } from 'react'
import useJwt from '@src/auth/jwt/useJwt'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink
} from 'reactstrap'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'
import CommonDataTable from '../ClientSideDataTable'

const OrderList = ({ getDashBoarddata }) => {
    const BusinessListsDrpDown = JSON.parse(localStorage.getItem('customerBusinesses'))
    const [TableDataLoading, setTableDataLoading] = useState(true)
    const [OrderList, setOrderList] = useState([])

    const getdata = (BusinessID) => {
        useJwt.VendorOrderListApi({ business_id: Number(BusinessID), page: 1, limit: 10 }).then(res => {
            // console.log(res.data.payload)
            setOrderList(res.data.payload)
            setTableDataLoading(false)
        }).catch(err => {
            console.log(err)
            setTableDataLoading(false)
        })
    }

    useEffect(() => {
        useJwt.customerbusinesslistbymobileno({ mobile: Number(JSON.parse(localStorage.getItem('userData')).mobile) }).then(res => {
            // console.log(res.data.payload)
            // localStorage.setItem('customerBusinesses', JSON.stringify(res.data.payload))
            const BusinessList = (res.data.payload)
            getdata(BusinessList[0].id)
        }).catch(err => {
            console.log(err)
        })
    }, [])
    const column = [
        {
            name: 'Order No',
            // minWidth: '100px',
            sortable: true,
            selector: 'id'
        },
        {
            name: 'Customer',
            minWidth: '150px',
            sortable: true,
            selector: 'oms_customer_name'
        },
        {
            name: 'Date',
            minWidth: '150px',
            sortable: true,
            selector: row => new Date(row.created_at).toLocaleDateString('fr-CA')
        },
        {
            name: 'Amount',
            // minWidth: '100px',
            sortable: true,
            selector: row => `${window.CURRENCY_SYMBOL} ${row.total_amount}`
        },
        {
            name: 'Status',
            minWidth: '100px',
            sortable: true,
            selector: row => {
                const statusBG = {
                    0: { title: 'Pending', color: 'light-danger' },
                    1: { title: 'Active', color: 'light-success' },
                    2: { title: 'Booked', color: 'light-success' },
                    3: { title: 'Collected', color: 'light-success' },
                    4: { title: 'Processing', color: 'light-success' },
                    5: { title: 'Stopped', color: 'danger' },
                    6: { title: 'Ready', color: 'light-success' },
                    7: { title: 'Processed', color: 'success' },
                    8: { title: 'Checked', color: 'light-success' },
                    9: { title: 'Paid', color: 'light-info' },
                    10: { title: 'Scanning', color: 'light-info' },
                    11: { title: 'Unpaid', color: 'light-warning' },
                    12: { title: 'Blocked', color: 'danger' },
                    13: { title: 'Invoice Pushed', color: 'light-info' },
                    14: { title: 'Shipping', color: 'light-info' },
                    15: { title: 'Delivered', color: 'success' }
                }
                return <Badge color={statusBG[row.status].color} pill className='px-1'>
                    {statusBG[row.status].title}
                </Badge>
            }
        }
    ]
    return (
        <Card>
            <CardHeader className='border-bottom'>
                <CardTitle tag='h4'>Order List</CardTitle>
                <CardTitle style={{ width: '150px' }}>
                    {/* <Label for="Business">Select a Business</Label> */}
                    {BusinessListsDrpDown.length > 1 ? <Select
                        theme={selectThemeColors}
                        maxMenuHeight={200}
                        className='react-select'
                        classNamePrefix='select'
                        maxMenuHeight={100}
                        defaultValue={BusinessListsDrpDown.map(x => { return { value: x.id, label: x.businessname } })[0]}
                        onChange={(selected) => {
                            setTableDataLoading(true)
                            getdata(selected.value)
                            getDashBoarddata(selected.value)
                        }}
                        options={BusinessListsDrpDown.map(x => { return { value: x.id, label: x.businessname } })}
                    /> : null}
                </CardTitle>
            </CardHeader>
            <CardBody>
                <Row>
                    <Col md='12'>
                        <CommonDataTable column={column} TableData={OrderList} TableDataLoading={TableDataLoading} />
                    </Col>
                </Row>
            </CardBody>
        </Card>
    )
}

export default OrderList