import { Card, CardBody, CardText, Button } from 'reactstrap'
import medal from '../../../assets/images/illustration/badge.svg'

const CardMedal = ({DashData}) => {
    return (
        <Card className='card-congratulations-medal'>
            <CardBody>
                <h5>Congratulations! {JSON.parse(localStorage.getItem('userData')).firstname || ''}</h5>
                <CardText className='font-small-3'>Revenue</CardText>
                <h3 className='mb-75 mt-2 pt-50'>
                    <a href='#' onClick={e => e.preventDefault()}>
                        {window.CURRENCY_SYMBOL} {DashData.total_sale || 0}
                    </a>
                </h3>
                {/* <Button.Ripple color='primary'>View</Button.Ripple> */}
                <img className='congratulation-medal' src={medal} alt='Medal Pic' />
            </CardBody>
        </Card>
    )
}

export default CardMedal
